import React, { useState, useEffect } from "react";
import Popup from "./Popup";

function MainPropChild1({ child1Category }) {
  const [showPopup, setShowPopup] = useState(false);
  const [description, setDescription] = useState([]);

  // show popup
  function showPopupHandler(categoryName) {
    setDescription(categoryName);
    setShowPopup(true);
  }

  // close popup
  function closePopupHandler() {
    setShowPopup(false);
  }
  return (
    <div className="prop-child1">
      <h5> Menu Items</h5>
      <div className="image-card">
        {showPopup && (
          <Popup
            closePopup={closePopupHandler}
            menuData={child1Category}
            description={description}
          />
        )}
        {child1Category.map((itm, k) => {
          return (
            <ul>
              <li>
                <img src={itm.strCategoryThumb} />
                <p onClick={() => showPopupHandler(itm.strCategory)}>
                  {itm.strCategory}
                </p>
                <div>
                  <span>{itm.strCategoryDescription}</span>
                </div>
              </li>
            </ul>
          );
        })}
      </div>
    </div>
  );
}

export default MainPropChild1;
