import React from 'react'

function Popup({closePopup,menuData,description}) {
    console.log('menuData',menuData)
    let itemDetails = menuData.filter((currentItemData)=>{
        return currentItemData.strCategory == description
    }).map((item)=>{
        return(
            <div className="popup-content-inner">
            <div className="popup-header">
                <img src={item.strCategoryThumb}/>
            </div>
            <h2>{item.strCategory}</h2>
            <h3>{item.strCategoryDescription}</h3>
            </div>
        )
    })
    return (
        <div>
            <div className="popup">
                <div className="popup-content">
                   {itemDetails}
                    <button>order now</button>
                    <h5 onClick={closePopup} className="popup-close"> close</h5>
                </div>
            </div>
        </div>
    )
}

export default Popup
